<script>
import api from '@/api'
import { getToken } from '@/api/auth'

export default ({
  inject: ['reload'],
  data () {
    return {
      columns: [
          {
            title: 'ID',
            key: 'id',
            width: 80, 
            align: 'center'
          },
          {
            title: '名称',
            key: 'name',
            width: 300, 
            align: 'center'
          },
          {
            title: '分类',
            key: 'classify',
            width: 120, 
            align: 'center',
            render: (h, params) => {
              const classify = params.row.classify;
              const text = classify == 1 ? '游戏' : classify == 2 ? '应用' : '-';
              const fontstyle = classify == (1 || 2) ? 'bold' : 'normal'
              return h('div', {
                style: {
                  fontWeight:fontstyle,
                },
              }, text)
            }
          },
          {
            title: '周浏览量',
            key: 'wview',
            width: 100, 
            align: 'center'
          },
          {
            title: '周下载数',
            key: 'wdown',
            width: 100, 
            align: 'center',
            render: (h, params) => {
              const text = params.row.wdown;
              return h('div', {
                style: {
                  color: '#ff6600'
                }
              }, text)
            }
          },
          {
            title: '月浏览量	',
            key: 'mview',
            width: 100, 
            align: 'center'
          },
          {
            title: '月下载数',
            key: 'mdown',
            width: 100, 
            align: 'center',
            render: (h, params) => {
              const text = params.row.mdown;
              return h('div', {
                style: {
                  color: '#ff6600'
                }
              }, text)
            }
          },
          {
            title: '总下载次数',
            key: 'tdown',
            width: 100, 
            align: 'center'
          },
          {
            title: '昨日点击数',
            key: 'ydown',
            width: 100, 
            align: 'center'
          },
          {
            title: '添加时间',
            key: 'addtime',
            width: '', 
            align: 'center'
          },
          {
            title: '编辑',
            key: 'addadmin_name',
            width: '', 
            align: 'center'
          },
      ],
      data: [],
      datePreSet: {
        shortcuts: [
          {
            text: '现在',
            value () {
              let d = new Date(),
                  str = '';
                  str += d.getFullYear() + '-';
                  str += d.getMonth() + 1 + '-';
                  str += d.getDate() + ' ';
                  str += d.getHours() + ':';
                  str += d.getMinutes() + ':';
                  str += d.getSeconds();
              return str;
            },
            onClick: () => {
              // this.$Message.info('Click today');
            }
          },
          {
            text: '昨天',
            value () {
              const date = new Date();
                    date.setTime(date.getTime() - 3600 * 1000 * 24);
              let str = '';
                  str += date.getFullYear() + '-';
                  str += date.getMonth() + 1 + '-';
                  str += date.getDate() + ' ';
                  str += date.getHours() + ':';
                  str += date.getMinutes() + ':';
                  str += date.getSeconds();
              return str;
            },
            onClick: () => {
              // this.$Message.info('Click yesterday');
            }
          },
          {
            text: '一周前',
            value () {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              let str = '';
                  str += date.getFullYear() + '-';
                  str += date.getMonth() + 1 + '-';
                  str += date.getDate() + ' ';
                  str += date.getHours() + ':';
                  str += date.getMinutes() + ':';
                  str += date.getSeconds();
              return str;
            },
            onClick: () => {
              // this.$Message.info('Click a week ago');
            }
          }
        ]
      },
      formData: {
        seltype: 'name',
        selval: '',
        stime: '',
        etime: '',
        classify: '1',
        gametype: '0',
        game_type: '0',
        app_type: '0',
        adminid: '0',
        order: '0',
      },
      isLoading: true,
      page: 1,
      per_page: 10,
      totalPages: 0,
      params: {
      },
      warnModal: false,
      modal_loading: false,
      tcount: '',
      game_type: '0',
      app_type: '0',
      adminlist: '0'
    }
    },
    mounted () {
      this.getData();
    },
    methods: {
      getData() {
        const token = getToken(),
              classify = this.formData.classify || 1,
              seltype = this.formData.seltype || 0,
              selval = this.formData.selval || '',
              stime = this.formData.stime || '',
              etime = this.formData.etime || '',
              page = this.page,
              per_page = this.per_page,
              gametype = this.formData.gametype || 1,
              adminid = this.formData.adminid || 0,
              order = this.formData.order || 0,
              params = {token, classify, seltype, selval, page, per_page, etime, stime, gametype, adminid, order}
              
        // console.log(params)
        api.statis(params).then(res => {
            if(res.code === 0) {
              this.data = res.data.gamelist
              this.tcount = res.data.tcount
              this.game_type = res.data.config.game_type
              this.app_type = res.data.config.app_type
              this.adminlist = res.data.adminlist
              this.totalPages = res.data.totlerows
              this.isLoading = false 
              // console.log(this.data)
              this.data.forEach(ele => {
                //格式化时间
                ele.addtime = this.$moment(ele.addtime*1000).format("YYYY-MM-DD k:mm");
              })
            } else {
              this.$Notice.error({title: res.msg})
            }
          })
        },
        changePage: function(page) {
          this.page = page;
          this.getData();
        },
        changePageNum: function(num) {
          this.per_page = num;
          this.getData();
        },
        setchData: function() {
            // this.params.status = this.statusModel
            this.getData()
        }
    }
})
</script>

<template>
    <div>
        <h3><Icon type="md-barcode" size=24 />统计列表</h3>
        
        <div class="formBox">
          <Select v-model="formData.seltype" style="width:100px">
              <Option value="name">名称</Option>
              <Option value="id">ID</Option>
          </Select>

          <div class="idbox"><Input v-model="formData.selval" clearable style="width: 200px" /></div>

          <span style="margin-left:20px;">添加时间</span>
          <DatePicker v-model="formData.stime" type="datetime" :options="datePreSet" placeholder="选择开始日期" style="width: 160px; margin-left: 20px;" ></DatePicker>
          <DatePicker v-model="formData.etime" type="datetime" :options="datePreSet" placeholder="选择结束日期" style="width: 160px; margin-left: 20px;" ></DatePicker>

          <Button type="primary" style="width:100px" @click="getData()">
              <Icon type="ios-search" size=16></Icon>
              搜索
          </Button>

          <Select v-model="formData.classify" style="width:100px" @on-change="setchData">
            <Option value="1">游戏</Option>
            <Option value="2">应用</Option>
          </Select>

          <Select v-if="formData.classify == 1" v-model="formData.gametype" style="width:100px" @on-change="setchData">
            <Option value="0">游戏类型</Option>
            <Option v-for="itm,idx in game_type" :value="idx" :key="idx">{{ itm }}</Option>
          </Select>
          <Select v-if="formData.classify == 2" v-model="formData.gametype" style="width:100px" @on-change="setchData">
            <Option value="0">应用类型</Option>
            <Option v-for="itm,idx in app_type" :value="idx" :key="idx">{{ itm }}</Option>
          </Select>
          

          <Select v-model="formData.adminid" style="width:100px" @on-change="setchData">
            <Option value="0">管理员</Option>
            <Option v-for="itm,idx in adminlist" :value="idx" :key="idx">{{ itm.username }}</Option>
          </Select>

          <Select v-model="formData.order" style="width:100px" @on-change="setchData">
            <Option value="0">排序</Option>
            <Option value="wdown">周下载</Option>
            <Option value="mdown">月下载</Option>
            <Option value="tdown">总下载</Option>
            <Option value="ydown">昨日点击数</Option>
          </Select>

          <span>当日总数: {{ tcount }}</span>
        </div>

        <Table 
            border 
            ref="selection" 
            :columns="columns" 
            :data="data" 
            :loading=isLoading
            >
        </Table>

        <div style="margin: 10px;overflow: hidden">
            <div style="float: right;">
                <Page :total="totalPages*1" show-total show-elevator show-sizer @on-page-size-change="changePageNum" @on-change="changePage" />
            </div>
        </div>
    </div>
</template>

<style lang="scss" scoped>
  .formBox {
    height: 50px;
    margin-bottom: 20px;
    .ivu-select-default{
      margin-right: 20px;
    }
    .searchBar{
      display: contents;
      padding-right: 20px;
    }
    .idbox {
        display:contents;
        span{
          margin-left:20px; 
        }
    }
    Button{
      margin-left: 20px;
      margin-right: 20px;
    }
    .ivu-modal{
      top: 200px !important;
    }
    
}
      
</style>